import ProjectDetails from "@/components/ProjectDetails/ProjectDetails";
import { projects } from "@/data/projectdata";
import { notFound } from "next/navigation";

interface Project {
  projectId: string;
  buttonUrl?: string;
  videoUrl?: string;
  title: string;
  thumbnail?: string;
  description?: string;
  shortDescription?: string;
  link?: undefined;
  linkText?: undefined;
  date?: string;
  lastUpdated?: undefined;
}

const MyWorks = ({ params }: { params: { projectId: string } }) => {
    const project = projects.find((p) => p.projectId === params.projectId);
    
  if (!project) {
    return notFound();
  }

  // Type assertion to match Project interface
  const typedProject: Project = {
    ...project,
    link: undefined,
    linkText: undefined,
    lastUpdated: undefined,
  };

  return <ProjectDetails project={typedProject} />;
};

export default MyWorks;
